import React from 'react';

const Login = () => {
    return (
        <section style={{height:"100vh", width:"100vw", backgroundColor:"#2b2f3b"}}>
        <div className="login d-flex justify-content-center align-items-center flex-column" style={{height:"100vh", width:"100vw"}}>
            <input placeholder="Enter Username"/>
            <button type="button">login </button>
        </div>
        </section>
    );
}

export default Login;
